.service {
  padding: 0 0 25px 0;

  &__wrapper {
    padding: 0 15px;
    margin: 0 auto;

    @media (min-width: @tablet-width) {
      padding: 0 29px;
    }
  }

  &__title {
    @media (min-width: @tablet-width) {
      padding: 25px 0;
    }
  }

  &__list {
    padding: 10px 0 30px 0;
    display: grid;
    row-gap: 10px;
  }

  &__item {
    display: flex;
    justify-content: space-between;
  }

  &__descr {
    text-align: left;
    padding-right: 10px;
  }

  &__price {
    text-align: right;
    white-space: nowrap;
  }
}
