.promo {
  padding: 17px 0 10px 0;

  &__wrapper {
    padding: 0 15px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(5, auto);
    grid-column-gap: 15px;

    @media (min-width: @tablet-width) {
      padding: 0 29px;
      grid-template-columns: repeat(6, 101px);
      grid-template-rows: repeat(3, auto);
      justify-content: space-between;
      column-gap: 20px;
    }
  }

  &__header {
    grid-column-start: 1;
    grid-column-end: -1;
    grid-row-start: 1;
    grid-row-end: 2;

    @media (min-width: @tablet-width) {
      grid-column-start: 1;
      grid-column-end: -1;
      grid-row-start: 1;
      grid-row-end: 2;
    }
  }

  &__title {
    font-family: "Cormorant Garamond", sans-serif;
    font-size: 25px;
    font-weight: 400;
    line-height: 34px;
    font-style: italic;
    text-align: left;

    &--up {
      font-style: normal;
      text-transform: uppercase;
    }

    @media (min-width: 347px) {
      font-size: 28px;
    }

    @media (min-width: @tablet-width) {
      font-size: 62px;
      line-height: 78px;
    }
  }

  &__picture {
    grid-column-start: 2;
    grid-column-end: -1;
    grid-row-start: 1;
    grid-row-end: 3;
    padding: 66px 0 0 0;
    justify-self: center;

    @media (min-width: @tablet-width) {
      padding: 153px 0 0 0;
      grid-column-start: 4;
      grid-column-end: -1;
      grid-row-start: 1;
      grid-row-end: 4;
    }
  }

  &__list {
    grid-column-start: 1;
    grid-column-end: -1;
    grid-row-start: 2;
    display: grid;
    grid-template-columns: repeat(auto-fill, 45px);
    column-gap: 4.5px;

    @media (min-width: @tablet-width) {
      grid-column-start: 1;
      grid-column-end: 5;
      grid-row-start: 3;
      grid-row-end: 4;
      grid-template-columns: repeat(auto-fill, 103px);
      column-gap: 18px;
    }
  }

  &__item {
    align-self: end;
  }

  &__img {
    display: inline-block;
    &--cropping-top-left {
      border-radius: 30px 0 0 0;
    }

    &--cropping-top-right {
      border-radius: 0 30px 0 0;
    }
  }

  &__btn,
  &__link {
    grid-column-start: 1;
    grid-column-end: -1;
    justify-self: center;
  }

  &__link {
    padding: 15px 0;

    @media (min-width: @tablet-width) {
      padding: 47px 0 57px 0;
      grid-column-start: 2;
      grid-column-end: 4;
      grid-row-start: 2;
      grid-row-end: 3;
    }
  }

  &__btn {
    padding-top: 41px;

    @media (min-width: @tablet-width) {
      padding: 40px 0 57px 0;
      grid-column-start: 1;
      grid-column-end: 2;
      grid-row-start: 2;
      grid-row-end: 3;
      justify-self: start;
    }
  }
}
