.statistics {
  padding: 25px 15px;

  &__list {
    margin: 0;
    display: grid;
    row-gap: 15px;

    @media (min-width: @tablet-width) {
      grid-template-columns: repeat(3, 1fr);
      column-gap: 110px;
    }
  }

  &__item {
    display: grid;
    row-gap: 15px;
  }

  &__indicator {
    font-family: "Cormorant Garamond", sans-serif;
    font-size: 24px;
    font-weight: 400;
    line-height: 29px;

    @media (min-width: @tablet-width) {
      font-size: 55px;
      line-height: 67px;
    }
  }

  &__descr {
    margin: 0;
  }

  @media (min-width: @tablet-width) {
      padding: 50px 29px;
  }
}
