.tabs {
  &__caption {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  &__content {
    display: none;

    &--active {
      padding: 10px 0;
      display: block;
    }
  }

  &__item {
    font-family: "Cormorant Garamond", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: #000000;
    border: none;
    outline: none;
    background-color: transparent;
    padding: 9px 0 4px 0;
    cursor: pointer;
    box-sizing: border-box;

    &--active {
      border-bottom: 2px solid @dark-grayish;
    }

    @media (min-width: @tablet-width) {
      font-size: 24px;
      line-height: 29px;
    }
  }
}
