.promotions {
  padding: 25px 0;

  &__title {
    font-family: "Cormorant Garamond", sans-serif;
    font-size: 24px;
    font-weight: 400;
    line-height: 29px;
    text-align: center;

    @media (min-width: @tablet-width) {
      font-size: 36px;
      line-height: 44px;
  }
  }

  &__item {
    display: grid;
    row-gap: 20px;
  }

  &__content {
    padding: 20px 15px 5px 15px;
    display: grid;
    row-gap: 15px;
  }

  &__img {
    width: 100vw;
  }
}
